@import '../../assets/scss/theme.scss';
@import '../../assets/fonts/simple-line-icons.css';

.search-sm {
  position: relative;
  width: 200px;
  input {
    background: none;
    outline: initial !important;
    border: 1px solid var(--bs-secondary);
    border-radius: 15px;
    padding: 0.25rem 0.75rem;
    font-size: 0.76rem;
    line-height: 1.5;
    color: var(--bs-primary);
    width: 100%;
    box-sizing: border-box;
  }

  /* Icon Styles */
  &:after {
    font-family: 'simple-line-icons';
    content: '\e090';
    font-size: 14px;
    color: var(--bs-secondary);
    position: absolute;
    right: 8px; /* Adjusted for better alignment */
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    pointer-events: none; /* Prevents click from interacting with the icon */
  }

  /* Focus styles */
  input:focus {
    border-color: var(--bs-primary);
    box-shadow: 0 0 3px rgba(var(--bs-primary), 0.3);
  }
}

/* Additional tweaks */
.search-sm::after {
  text-align: center;
}

/* Pagination */
.pagination-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
}

.page-item {
  padding: 5px;

  .page-link {
    outline: initial !important;
    box-shadow: initial !important;
    line-height: 1.5;
    border-radius: 37px !important;
    min-width: 35px;
    text-align: center;
    height: 35px;
    padding: 0.5rem 0;
    background-color: transparent;
    border-color: transparent;

    &.next {
      background: var(--bs-secondary);
      color: $white;
      border: 1px solid var(--bs-secondary);
    }

    &.prev,
    &.prev {
      background: var(--bs-secondary);
      border: 1px solid var(--bs-secondary);
      color: $white;
    }

    &.first,
    &.last {
      background: transparent;
      color: var(--bs-primary);
      border: 1px solid var(--bs-secondary);
      border-radius: 30px;

      &:hover {
        background: var(--bs-secondary);
        color: $white;
        border: 1px solid var(--bs-secondary);
      }
    }

    &:hover {
      background-color: transparent;
      border-color: var(--bs-secondary, lighten($secondary, 10%));
      color: var(--bs-secondary);
    }
  }

  &.active .page-link {
    background: transparent;
    border: 1px solid var(--bs-secondary);
    color: var(--bs-secondary);
  }

  &.disabled .page-link {
    border-color: var(--bs-secondary);
    color: var(--bs-secondary);
    background: transparent;
    opacity: 0.3;
  }
}

.page-link {
  background-color: transparent;
  border-color: transparent;
  color: var(--bs-primary);
}

input[type='checkbox'] {
  border-color: var(--bs-primary);
}

input[type='checkbox']:checked {
  border-color: var(--bs-primary);
  background-color: var(--bs-primary);
}

input[type='checkbox']:focus {
  border-color: var(--bs-primary);
}

.pointer-cursor {
  cursor: pointer;
}

.pointer-cursor:hover {
  cursor: pointer;
  color: var(--bs-primary) !important;
}

.r-table {
  thead {
    th {
      &.sorted-asc {
        box-shadow: inset 0 3px 0 0 var(--bs-secondary);
      }

      &.sorted-desc {
        box-shadow: inset 0 -3px 0 0 var(--bs-secondary);
      }
    }
  }
}

.top-right-button-container {
  float: right;
  position: relative;
}

.filter-dropdown {
  position: absolute;
  background: white;
  border: 1px solid #ccc;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
  padding: 10px;
  z-index: 1000;
}

.filter-option {
  display: flex;
  align-items: center;
  gap: 5px;
  margin-bottom: 5px;
  cursor: pointer;
}

.active-filter {
  color: var(--bs-secondary) !important;
}
