*::-webkit-scrollbar {
  width: 2px;
  height: 2px; 
}

*::-webkit-scrollbar-track {
  background: #f0f0f0;
  border-radius: 10px;
}

*::-webkit-scrollbar-thumb {
  background: #281251; 
  border-radius: 10px;
  border: 2px solid #f0f0f0; 
}

*::-webkit-scrollbar-thumb:hover {
  background: #281251;
}

* {
  scrollbar-width: thin;
  scrollbar-color: #332156 #f0f0f0;
}
