@import '../../assets/scss/theme.scss';

.datetime-selector {
  position: relative;
  display: inline-block;

  .dropdown-button {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px 12px;
    border: 1px solid #ccc;
    background: white;
    cursor: pointer;
    min-width: 250px;
  }

  .dropdown-menu {
    position: absolute;
    top: 100%;
    left: 0;
    background: white;
    border: 1px solid #ddd;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    z-index: 1000;
    display: flex;
    flex-direction: row;
    min-width: 160px;
  }

  .dropdown-left {
    padding: 4px 0;
    width: 160px;
  }

  .dropdown-item {
    width: 100%;
    padding: 2px 10px;
    text-align: left;
    cursor: pointer;
    font-size: 12px;

    &:hover {
      background: var(--bs-secondary);
      color: #fff;
    }
  }

  .divider {
    height: 1px;
    background: #ddd;
    margin: 3px 0;
  }

  .custom-picker {
    width: 234px;
    background: white;
    border-left: 1px solid #ddd;
    padding: 12px;
    display: flex;
    flex-direction: column;
  }

  .picker-row {
    margin-bottom: 12px;
    font-size: 12px;
  }

  .action-buttons {
    display: flex;
    justify-content: flex-end;
    gap: 8px;
  }

  .dropdown-item.selected {
    background-color: var(--bs-primary);
    color: white;
  }

  .error-text {
    color: red;
    font-size: 12px;
    margin-bottom: 5px;
  }
}

.flatpickr-wrapper {
  position: relative;
  display: flex;
  align-items: center;

  .form-control {
    padding-left: 2rem;
  }

  .calendar-icon {
    position: absolute;
    left: 8px;
    font-size: 16px;
    color: var(--bs-secondary);
  }
}
